body,
html {
  padding: 10px;
  margin: 0 auto;
  color: #181a1b;
  max-width: 960px;
  background: #f8f9fb;

  font-weight: 400;
  font-style: normal;
  font-variant: normal;
  font-feature-settings: normal;
  text-rendering: optimizeLegibility;
  font-family: AtlasGrotesk, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

h1 {
  font-size: 28px;
  line-height: 1.5;
  font-family: "Calluna";
}

a {
  color: black;
  transition: all 0.2s;
  text-decoration: none;
  -webkit-transition: all 0.2s;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

a:hover {
  color: #00e;
}

p {
  line-height: 1.75em;
}

img {
  width: 100%;
  height: auto;
}

.header {
  display: flex;
  max-width: 920px;
  margin-bottom: 70px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}

.header nav ul {
  margin: 0;
  padding: 0;
  gap: 1rem;
  display: flex;
  list-style-type: none;
}

.header nav ul li {
  margin: 0 15px 0 0;
}

.header nav ul li:last-child {
  margin: 0;
}

.nav-title {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.3;
  font-family: "AtlasGrotesk";
}

.nav-title a {
  color: black;
  border: none;
}

.nav-subtitle {
  top: -4px;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  position: relative;
}

.page-title {
  font-size: 26px;
  line-height: 1.5;
  text-align: start;
  font-family: "Calluna";
}

.wrapper {
  display: flex;
  flex-direction: row;
}

.left {
  width: 60%;
  display: inline-block;
  padding-right: 30px;
}

.right {
  width: 40%;
  display: inline-block;
  padding-left: 30px;
}

.skills {
  margin: 0;
  display: flex;
  font-size: 1em;
  color: #134db6;
  margin-bottom: 10px;
  letter-spacing: 0.2em;
  flex-direction: column;
  text-transform: uppercase;
}

.icons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.icons i {
  font-size: 25px;
  margin-right: 15px;
}

.icons img {
  width: 25px;
  height: 25px;
}

.seagulls {
  background-image: url("/images/seagulls.svg");
  width: 266px;
  height: 75px;
  opacity: 5%;
  display: block;
  position: absolute;
  margin-top: 30px;
  margin-left: 20px;
}

.post_back {
  top: -10px;
  border: none;
  position: relative;
}

li {
  margin-bottom: 10px;
}

.projects {
  gap: 30px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}

.projects li {
  flex-basis: 48%;
}

.project {
  height: 300px;
  display: block;
  overflow: hidden;
  position: relative;
  border-radius: 8px;
  text-decoration: none;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.thumbnail {
  width: 100%;
  height: auto;
  filter: brightness(95%);
}

.project-overlay {
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  position: absolute;
  border-radius: 8px;
  background-color: #fff;
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.project:hover .project-overlay {
  transform: translateY(0);
}

.project-header {
  display: flex;
  position: relative;
  align-items: center;
  padding: 1.5em 2em;
  background-color: #fff;
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
  border-radius: 8px 0px 8px 8px;
}

.project-arc {
  right: 0;
  z-index: 1;
  width: 80px;
  height: 80px;
  bottom: 100%;
  position: absolute;
}

.project-arc path {
  fill: #fff;
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}

.project:hover .project-header {
  transform: translateY(0);
}

.project-title {
  margin: 0;
  font-size: 1.15em;
  text-align: center;
  font-family: "Calluna";
  color: rgba(0, 0, 0, 0.85);
}

.project-description {
  margin: 0;
  overflow: hidden;
  padding: 0 2em 1.5em;
  color: rgba(0, 0, 0, 0.8);
}

footer {
  display: flex;
  font-size: 1em;
  margin-top: -30px;
  align-items: center;
  margin-bottom: 1rem;
  justify-content: space-between;
}

footer ul {
  padding: 0;
  display: flex;
  margin-top: 20px;
  align-items: center;
  list-style-type: none;
}

footer ul a {
  border: none;
}

footer ul li i {
  font-size: 25px;
  margin-right: 20px;
}

hr {
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin: 3rem 0;
}

@media (max-width: 576px) {
  .header {
    text-align: center;
    margin-bottom: 50px;
    flex-direction: column;
    justify-content: center;
  }

  nav {
    margin-top: 30px;
  }

  .page-title {
    font-size: 22px;
    text-align: center;
  }

  .wrapper {
    flex-direction: column;
  }

  .left {
    padding: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  .right {
    padding: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  .seagulls {
    margin-top: -120px;
    margin-left: 210px;
  }

  footer {
    font-size: 0.8em;
  }
}

@media (max-width: 788px) {
  .project {
    height: 280px;
  }

  .projects li {
    flex-basis: 100%;
  }
}
